import { loginBegin } from "../../api/loginBegin";
import { loginComplete } from "../../api/loginComplete";
import base64url from "base64url";

export async function loginWebauthn(email:string): Promise<any> {
  const apikey=this.apikey
  //api hit login begin 
  function bufferDecode(value) {
      return Uint8Array.from(atob(value), c => c.charCodeAt(0));
  }
  function bufferEncode(value) {
      return btoa(String.fromCharCode.apply(null, new Uint8Array(value)))
        .replace(/\+/g, "-")
        .replace(/\//g, "_")
        .replace(/=/g, "");;
  }
  const options = await loginBegin(apikey,email);
  // get state id
  const state_id=options.state_id;
  if (options.publicKey.allowCredentials.length === 0) {
    console.info('No registered credentials found.');
    return Promise.resolve(null);
  }

  options.publicKey.challenge = bufferDecode(options.publicKey.challenge);

  for (let cred of options.publicKey.allowCredentials) {
    cred.id = bufferDecode(cred.id);
  }

  const cred:any = await navigator.credentials.get({
    publicKey: options.publicKey
  });

  const credential={id:null,rawId:null,type:null,response:null};
  credential.id = cred.id;
  credential.type = cred.type;
  credential.rawId = bufferEncode(cred.rawId);

  if (cred.response) {
    const clientDataJSON =
      bufferEncode(cred.response.clientDataJSON);
    const authenticatorData =
      bufferEncode(cred.response.authenticatorData);
    const signature =
      bufferEncode(cred.response.signature);
    const userHandle =
      bufferEncode(cred.response.userHandle);
    credential.response = {
      clientDataJSON,
      authenticatorData,
      signature,
      userHandle,
    };
  }

  //api hit login finish
  let promise=new Promise(function(resolve,reject){
  resolve(loginComplete(apikey,email,state_id,credential.id,credential.rawId,credential.type,credential.response.authenticatorData,credential.response.clientDataJSON,credential.response.signature,credential.response.userHandle));
  })
  return promise 
};