export async function signIn(): Promise<any> {

    const apikey = this.apikey;
    const webauthn_forced=this.config?.enable_webauthn;
	const language= this.config?.language;
    const source = this.config?.source;
    function setHttp(link) {
		if (link.search(/^http[s]?\:\/\//) == -1) {
			link = 'http://' + link;
		}
		return link;
	}
	const redirect_url = this.config?.redirect_url?setHttp(this.config?.redirect_url):null;


    let projectConfig;
	try {
		projectConfig = await getProjectConfig(apikey, language);
	} catch (e) {
		console.error(e.message);
		return;
	}

    let promise=new Promise(function(resolve,reject){
        if(source && (source.find((obj1)=>{ return obj1.type==='phone' &&  obj1.feature==='otp'})?true:false) && (source.find((obj2)=>{return obj2.type==='email' && obj2.feature==='otp'})?true:false)){
            
            signInWithPhoneOTP(apikey,redirect_url,language,false, 'otp').then(response=>{resolve(response)});
            
        }
        else if(source && (source.find((obj)=>{return obj.type==='phone' &&  obj.feature==='otp'})?true:false) && (source.find((obj)=>{return obj.type==='email' && obj.feature==='magiclink'})?true:false)){
           
            signInWithPhoneOTP(apikey,redirect_url,language,false, 'magiclink').then(response=>{resolve(response)});
        }
        else if(source && source.find((obj)=>{ return obj.type==='email' && obj.feature==='magiclink'})?true:false){
           
            signInWithMagicLink(apikey,redirect_url,language).then(response=>{resolve(response)});
        }
        else if(source && source.find((obj)=>{return obj.type==='email' && obj.feature==='otp'})?true:false){
          
            signInWithEmailOTP(apikey,redirect_url,language).then(response=>{resolve(response)});
        }
        else if(source && source.find((obj)=>{return obj.type==='phone' && obj.feature==='otp'})?true:false){
           
            signInWithPhoneOTP(apikey,redirect_url,language, true).then(response=>{resolve(response)});
        }
        else {
           
            signInWithMagicLink(apikey,redirect_url,language).then(response=>{resolve(response)});
        }
       
    })
   
    return promise
}
