import { request } from '../helpers/request';
import { getConfig } from '../config';

const url = `${getConfig().apiUrl}/webauthn/login/finish`;

export function loginComplete(apikey:string,identifier:string, state_id:string,id:string,rawId:string, type:string, authenticatorData:string, clientDataJSON:string, signature:string, userHandle:string): Promise<any> {
	
	return request({
		method: 'POST',
		credentials:"include",
		url,
        queryParams: {
			identifier,
			state_id
		},
        body:{
            id:id,
			rawId:rawId,
			type:type,
			response:{
				authenticatorData:authenticatorData,
				clientDataJSON: clientDataJSON,
				signature:signature,
				userHandle:userHandle
			}
        },
		headers: {
			'X-API-Key': apikey,
		},
	});
}