import { registerationBegin } from "../../api/registerationBegin";
import base64url from "base64url";
import { registerationComplete } from "../../api/registerationComplete";
import { webauthnFlagUpdate } from "../../api/webauthnFlagUpdate";
export async function enableWebauthn(access_token:string, api_key?:string): Promise<any> {
  const apikey=api_key?api_key:this.apikey
  //api hit registeration begin
  if(access_token===undefined||access_token===null) {access_token=""};
  function bufferDecode(value) {
      return Uint8Array.from(atob(value), c => c.charCodeAt(0));
    }
    function bufferEncode(value) {
      return btoa(String.fromCharCode.apply(null, new Uint8Array(value)))
        .replace(/\+/g, "-")
        .replace(/\//g, "_")
        .replace(/=/g, "");;
    }
  const options = await registerationBegin(apikey,access_token)
  const state_id=options.state_id;
  options.publicKey.user.id = bufferDecode(options.publicKey.user.id);
  options.publicKey.challenge = bufferDecode(options.publicKey.challenge);

  if (options.publicKey.excludeCredentials) {
    for (let cred of options.publicKey.excludeCredentials) {
      cred.id = bufferDecode(cred.id);
    }
  }
  if(options.publicKey.user.name){
    const cred:any = await navigator.credentials.create({
      publicKey: options.publicKey
    });
  
      


  const credential={id:null,rawId:null,type:null,response:null};
  credential.id =     cred.id;
  credential.rawId =  bufferEncode(cred.rawId);
  credential.type =   cred.type;

  if (cred.response) {
    const clientDataJSON =
      bufferEncode(cred.response.clientDataJSON);
    const attestationObject =
      bufferEncode(cred.response.attestationObject);
    credential.response = {
      clientDataJSON,
      attestationObject
    };
  }
  localStorage.setItem(`credId`, credential.id);
  //api hit registeration finish
  let promise=new Promise(function(resolve,reject){
    registerationComplete(apikey,access_token,state_id,credential.id,credential.rawId,credential.type,credential.response.attestationObject,credential.response.clientDataJSON).then(async response=>{
      if(response.code)reject(response);
      else {
        await webauthnFlagUpdate(apikey, access_token)
        resolve(response)
      }})
    .catch((error)=>{
      console.log(error)
      reject()
    })
})
  
  return promise  
}else return null 
}