import { request } from '../helpers/request';
import { getConfig } from '../config';


const url = `${getConfig().apiUrl}/webauthn/registration/finish`;

export function registerationComplete(apikey:string, access_token:string,state_id:string,id:string,rawId:string, type:string, attestationObject:string, clientDataJSON:string): Promise<any> {
	
	return request({
		method: 'POST',
		credentials:"include",
		url,
        queryParams: {
			state_id
		},
        body:{
            id:id,
			rawId:rawId,
			type:type,
			response:{
				attestationObject:attestationObject,
				clientDataJSON: clientDataJSON
			}
        },
		headers: {
			'X-API-Key': apikey,
			Authorization: `Bearer ${access_token}`,
		},
	});
}
